import React, { Component } from 'react';
import { Form, Icon, Input, Button } from 'antd';
import { Redirect } from 'react-router-dom';
import background from '../../assets/images/login.jpg';
import { http, appService } from '../../services';

const FormItem = Form.Item;

class LoginForm extends Component {

	state = {
		isAuthenticated: false,
		loading: false
	}

	componentDidMount() {
		const token = appService.getToken();
		const user = appService.getUser();
		if (token && user) {
			this.setState({ isAuthenticated: true });
		}
	}

	handleSubmit = (e) => {
		e.preventDefault();
		this.setState({ loading: true });
		this.props.form.validateFields((err, values) => {
			if (!err) {
				http.post('auth/login', values).then(data => {
					appService.setToken(data.token);
					appService.setUser(data.user);
					this.setState({
						isAuthenticated: true,
						loading: false
					});
				}).catch(() => this.setState({ loading: false }));
			}
		});
	}

	render() {
		const { getFieldDecorator } = this.props.form;
		const style = {
			background: 'url(' + background + ')',
			backgroundSize: 'cover',
			minHeight: '100vh'
		};

		if (this.state.isAuthenticated) {
			return <Redirect to="/dashboard" />;
		}

		return (
			<div className="content-wrapper" style={style}>
				<div className="auth-form-wrapper">
					<h3 className="text-center">Login Laundry App</h3>
					<Form onSubmit={this.handleSubmit} className="m-t-20">
						<FormItem>
							{getFieldDecorator('email', {
								rules: [{ required: true, message: 'Email harus diisi' }],
							})(
								<Input prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="email" type="email" />
							)}
						</FormItem>
						<FormItem>
							{getFieldDecorator('password', {
								rules: [{ required: true, message: 'Password harus diisi' }],
							})(
								<Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
							)}
						</FormItem>
						<FormItem className="text-center">
							{ this.state.loading ? <Button type="primary" shape="circle" loading /> :
								<Button type="primary" size="large" htmlType="submit" className="btn-block">
									Login
								</Button>
							}
							<div className="text-center">
								<a href="">Lupa password ?</a>
							</div>
						</FormItem>
					</Form>
				</div>
			</div>
		);
	}
}

const Login = Form.create()(LoginForm);

export default Login;



